import { Button, Img, Input, Text } from "components";
import React from "react";

const NewsLetter = () => {
  return (
    <>
      <div className="bg-blue-200 flex items-center max-w-[110rem] mx-auto p-[4rem] sm:py-12 sm:px-5 rounded-lg sm:rounded-none">
        <div className="flex sm:flex-col flex-row gap-[4rem] sm:space-x-0 sm:gap-[5rem] sm:w-full">
          {/* left side text */}
          <div className="flex flex-col items-start justify-start w-[50%] sm:w-full">
            <Text
              className="text-indigo-900 text-xl"
              size="txtPublicSansLight18Indigo900"
            >
              NEWSLETTER
            </Text>
            <Text
              className="mt-[5px] text-5xl sm:text-[3rem] text-gray-600 w-full"
              size="txtMerriweatherBlack36"
            >
              Subscribe to our website newsletter to receive news and updates.
            </Text>
            <Text
              className="mt-5 text-bluegray-900 text-2xl"
              size="txtPublicSansLight18"
            >
              Get special offers directly to your email every week!
            </Text>
          </div>

          {/* news letter form */}
          <div className="flex flex-col gap-[1rem] justify-start w-[50%] sm:w-full">
            <div className="flex flex-col gap-5 items-start justify-start w-full">
              <Input
                name="field"
                placeholder="Your Email"
                className="font-bold leading-[normal] p-0 placeholder:text-indigo-900_79 text-left text-xl w-full"
                wrapClassName="border border-indigo-900 border-solid w-full"
                type="email"
                color="white_A700"
                size="sm"
                variant="fill"
              ></Input>
              <Button
                className="cursor-pointer font-semibold leading-[normal] min-w-[13rem] text-center text-xl"
                shape="round"
                color="indigo_900"
                size="xl"
              >
                Subscribe
              </Button>
            </div>
            <Button
              className="flex cursor-default h-[7rem] w-[7rem] items-center justify-center bg-blue-400 rounded-[50%]"
              color="pink_300"
              size="sm"
            >
              <Img
                className="h-[5rem]"
                src="images/img_ticket_77X77.svg"
                alt="ticket One"
              />
            </Button>
          </div>
        </div>
      </div>
    </>
  );
};

export default NewsLetter;
